import { Component } from '@angular/core';
import { IonicPage,NavController,LoadingController,NavParams,ModalController,ViewController} from 'ionic-angular';
import { Http } from '@angular/http';

@IonicPage()
@Component({
  selector: 'page-vehicle-type',
  templateUrl: 'vehicle-type.html',
})


export class VehicleTypePage {
	 searchQuery: string = '';
    vTypes={
      "C":     [
        {
          "name":"Car",
          "code":"CR"
        },
        {
          "name":"Van",
          "code":"VN"
        },
        {
          "name":"Jeep",
          "code":"JP"
        },
        {
          "name":"Crew Cab",
          "code":"CC"
        },
        {
          "name":"Pickup",
          "code":"PU"
        },
        {
          "name":"Bus",
          "code":"BS"
        },
        {
          "name":"Lorry",
          "code":"LR"
        },
        {
          "name":"Truck",
          "code":"TK"
        },
        {
          "name":"Three Wheel",
          "code":"TW"
        },
        {
          "name":"Tractor",
          "code":"TR"
        },
        {
          "name":"Heavy-duty",
          "code":"HD"
        },
        {
          "name":"Motor Bikes",
          "code":"MB"
        },
        {
          "name":"Hybrid",
          "code":"HB"
        },
        {
          "name":"Electric",
          "code":"EL"
        }
      ]
    };
    selectedType={};

  constructor(public navCtrl: NavController,public navParams: NavParams,public viewCtrl:ViewController ) {}
      
    dismiss(){
      this.viewCtrl.dismiss(this.selectedType);
    }
    selectType(logo) {
      this.selectedType=logo;
      this.viewCtrl.dismiss(this.selectedType);
    }




}
  



